/* Add transitions for smoother effects */
.App-header {
  background-color: #880808;
  min-height: 100vh; /* Use min-height to ensure the header takes up at least the viewport height */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  opacity: 1;
  transition: opacity 1s ease;
}

/* Navbar and White Page Transition */
.Navbar {
  height: 0;
  overflow: hidden;
  transition: height 1s ease; /* Add height transition */
}

.White-page {
  background-color: linear-gradient(135deg, #3498db, #8e44ad);
  width: 100%;
  height: 0;
  overflow: hidden;
  transition: height 1s ease; /* Add height transition */
  animation: gradientAnimation 10s ease infinite;
}

/* Animation for expanding the Navbar and White Page */
@keyframes expandAnimation {
  from {
    height: 0;
  }
  to {
    height: 100%;
  }
}

/* Animation for expanding the Navbar and White Page */
.expand {
  height: 100%;
}


/* Animation for fading in */
.fade-in {
  opacity: 1;
}

/* Animation for fading out */
.fade-out {
  opacity: 0;
}

/* Adjust timing as needed */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

/* Gradient Background */
.App {
  background: linear-gradient(135deg, #3498db, #8e44ad); /* Adjust gradient colors */
  background-size: 200% 200%;
  animation: gradientAnimation 10s ease infinite;
}

@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* Pattern Overlay */
.pattern-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACAAAAAgCAYAAABzenr0AAAAgElEQVRIDbWMAQYVG4gtGzMBMQAQiEwsYxEbSECSkiyTBk/hsQBuAHslzIIjGzCAAAAASUVORK5CYII=');
  opacity: 0.1; /* Adjust opacity as needed */
  pointer-events: none; /* Prevent interactions with the overlay */
}
